import React from "react";
import accounts from "../accounts";
import months from "../lists";
import images from "./functions/images";

function setDate(x) {
  const date = x.split("-");
  const day = date[2];
  const month = months[parseInt(date[1], 10) - 1];
  const year = date[0];

  return month + " " + day + ", " + year;
}

function BookingLine(props) {
  let img = accounts[0].icon;
  for (let i = 1; i < accounts.length; i++) {
    if (props.platform === accounts[i].code) {
      img = accounts[i].icon;
    }
  }
  return (
    <div className="reserv-line fade-in-1" onClick={props.onClick}>
      <div className="reserv-line-img-info">
        <img
          className="account-img fade-in-box"
          src={img}
          alt="account_image"
        />
        <div className="reserv-line-info fade-in-2">
          <h4>{props.platform_reserv_code}</h4>
          <p>
            {setDate(props.check_in)} | {props.num_nights} Night(s)
          </p>
        </div>
      </div>

      <img
        className="arrow-icon fade-in-1"
        src={images.icon_arrow}
        alt="arrow"
      />
    </div>
  );
}

export { setDate };

export default BookingLine;
