import { useEffect, useState } from "react";
import "./App.css";
import NavBar from "./components/NavBar";
import Notch from "./components/Notch";
import Home from "./components/Home";
import Page_Bookings from "./components/AllBookings";
import BookingDetails from "./components/BookingsPage/BookingDetails";
import StatsPage from "./components/StatsPage/StatsPage";

const COLORS = ["#2a9d8f", "#264653", "#e9c46a", "#f4a261", "#e76f51"];

function App() {
  const [activePage, setActivePage] = useState("home");
  const [selectedBooking, setSelectedBooking] = useState(null);

  useEffect(() => {
    const mobileRect = document.querySelector(".mobile-Rect");
    if (mobileRect) {
      mobileRect.scrollTo({ top: 0, behavior: "smooth" });
    }
  }, [activePage]);

  return (
    <div className="App">
      <div className="dotted-background"></div>
      <div className="mobile-Rect custom-cursor">
        <Notch />
        <div className="mobile-content">
          {activePage === "home" && (
            <Home
              setSelectedBooking={setSelectedBooking}
              setActivePage={setActivePage}
            />
          )}
          {activePage === "bookings" && (
            <Page_Bookings
              setSelectedBooking={setSelectedBooking}
              setActivePage={setActivePage}
            />
          )}
          {activePage === "details" && selectedBooking && (
            <BookingDetails
              booking={selectedBooking}
              setActivePage={setActivePage}
              setSelectedBooking={setSelectedBooking}
            />
          )}
          {activePage === "stats" && <StatsPage name="yes" />}
          <br></br>
        </div>

        <NavBar setActivePage={setActivePage} activePage={activePage} />
        <br></br>
      </div>
    </div>
  );
}

export { COLORS };

export default App;
