import React, { useState } from "react";
import bookings from "../bookings";
import BookingLine from "./BookingLine";
import ComponentTitle from "./ComponentTitle";
import ChipsAccount, {
  chipOptionsAccounts,
} from "./BookingsPage/ChipsAccounts";

function Page_Bookings({ setSelectedBooking, setActivePage }) {
  const [activeOption, setActiveOption] = useState("All");

  let filteredBookings = bookings;
  if (activeOption !== chipOptionsAccounts[0]) {
    filteredBookings = bookings.filter(
      (booking) => booking.platform === activeOption
    );
  }
  return (
    <div>
      <br></br>
      <div className="details-header fade-in-box">
        <div className="details-title">
          <h1>All Bookings</h1>
          <p className="text-left-align">
            Easily browse through all your reservations, sorted by check-in
            date. Use the filters to view bookings from specific platforms.
          </p>
        </div>
      </div>
      <hr />
      <ChipsAccount
        activeOption={activeOption}
        setActiveOption={setActiveOption}
      />

      {filteredBookings.length > 0 ? (
        filteredBookings.map((booking) => (
          <BookingLine
            key={booking.platform_reserv_code}
            {...booking}
            onClick={() => {
              setSelectedBooking(booking);
              setActivePage("details");
            }}
          />
        ))
      ) : (
        <h4>No bookings found for {activeOption}.</h4>
      )}
    </div>
  );
}

export default Page_Bookings;
