import { useState } from "react";
import Accounts from "./Accounts_front_page";
import AllRecordingsImage from "./AllRecordingsImage";
import Header from "./Header";
import LatestBookings from "./LatestBookings";
import NewBookingNot from "./NewBooking";
import bookings from "../bookings";

function Home({ setSelectedBooking, setActivePage }) {
  const [showNewBooking, setShowNewBooking] = useState(true);
  
  const handleNewBookingClick = () => {
    setSelectedBooking(bookings[21]);
    setActivePage("details");
    setShowNewBooking(false);
  }

  return (
    <div>
      <Header />
      {showNewBooking && (
        <NewBookingNot onDismiss={handleNewBookingClick} />
      )}
      <AllRecordingsImage setActivePage={setActivePage}/>
      <Accounts />
      <LatestBookings
        setSelectedBooking={setSelectedBooking}
        setActivePage={setActivePage}
      />
    </div>
  );
}

export default Home;
