import React from "react";
import "./BookingsPage.css";

const chipOptionsAccounts = ["All","Booking", "Airbnb", "Email"];

function ChipsAccount({activeOption, setActiveOption}) {

  return (
    <div className="chip-div fade-in-box">
      {chipOptionsAccounts.map((option) => (
        <h4
          key={option}
          className={`chip ${activeOption === option ? "active" : ""}`}
          onClick={() => setActiveOption(option)}
        >
          {option}
        </h4>
      ))}
    </div>
  );
}

export {chipOptionsAccounts};

export default ChipsAccount;
