import React from "react";
import accounts from "../accounts";
import ComponentTitle from "./ComponentTitle";
import bookings from "../bookings";

function countBookingAcc(account)
{
  let filteredBookings = bookings.filter(
        (booking) => booking.platform === account
      );

      return filteredBookings.length;
}

function CardHalf(props) {
  return (
    <div className="account-box fade-in-box">
      <img className="account-img fade-in-1" src={props.icon} alt="image_booking" />
      <br></br>
      <h4 className="fade-in-1">{props.name}</h4>
      <p className="fade-in-2">{countBookingAcc(props.code)} Reservations</p>
    </div>
  );
}

function Accounts() {
  return (
    <div className="accounts-div">
      <ComponentTitle name="Accounts" />
      <div className="accounts-box-half">
        {accounts.slice(0, 2).map((account) => (
          <CardHalf key={account.code} {...account} />
        ))}
      </div>
      <div className="account-box-line">
        {accounts.slice(2, 3).map((account) => (
          <CardHalf key={account.code} {...account} />
        ))}
      </div>
    </div>
  );
}

export {countBookingAcc};

export default Accounts;
