import React from "react";
import accounts from "../../accounts";
import BookingLine, { setDate } from "../BookingLine";
import ComponentTitle from "../ComponentTitle";
import Timeline from "./Timeline";
import bookings from "../../bookings";
import InfoLine from "../InfoLine";
import images from "../functions/images";

function BookingDetails({ booking, setActivePage, setSelectedBooking }) {
  let img = accounts[0].icon;
  for (let i = 0; i < accounts.length; i++) {
    if (booking.platform === accounts[i].code) {
      img = accounts[i].icon;
    }
  }

  const userOtherBookings = bookings.filter(
    (b) => b.email === booking.email && b.platform_reserv_code !== booking.platform_reserv_code
  );

  let classPayment = "payment-box-uncompleted";
  let infoPayment = "Not Completed";
  let imgPayment = images.icon_x;
  if (booking.payment_status === "paid") {
    classPayment = "payment-box-completed";
    infoPayment = "Completed";
    imgPayment = images.icon_checked;
  }

  return (
    <div>
      <br></br>
      <div className="details-header"><button className="buttonBack" onClick={() => setActivePage("bookings")}>← Back</button></div>
      <div className="details-header fade-in-1">
        <div className="details-title">
          <h1 className="fade-in-2">{booking.name}</h1>
          <h4>{booking.platform_reserv_code}</h4>
        </div>
        <div className="details-title">
          <img className="account-img" src={img} alt="account_image" />
        </div>
      </div>
      <hr></hr>
      <ComponentTitle name="Check-in Details" />
      <Timeline booking={booking} />
      <br />
      <ComponentTitle name="Details" />
      <div className="details-header">
        <div className="detail-box-50 fade-in-1">
          <img src={images.icon_user} alt="user" />
          <p>Number of persons</p>
          <h4>{booking.num_people}</h4>
        </div>
        <div className="detail-box-50 fade-in-2">
          <img src={images.icon_night} alt="user" />
          <p>Number of nights</p>
          <h4>{booking.num_nights}</h4>
        </div>
      </div>
      <br />
      <ComponentTitle name="Contact Information" />
      <div className="details-person-box fade-in-1">
        <InfoLine title="Name" info={booking.name} />
        <hr className="details-hr" />
        <InfoLine title="Email" info={booking.email} />
        <hr className="details-hr" />
        <InfoLine title="Tel. Number" info={booking.phone} />
        <hr className="details-hr" />
        <InfoLine title="Booking Date" info={setDate(booking.booking_date)} />
      </div>
      <br />
      <ComponentTitle name="Payment Status" />
      <div className={classPayment}>
        <div className="details-header">
          <div className="details-header">
            <img src={imgPayment} alt="user" className="payment-img" />
            <h3>{infoPayment}</h3>
          </div>
          <div>
            <h3>{booking.price_total} €</h3>
          </div>
        </div>
      </div>
      <br />
      <ComponentTitle name="Other Bookings" />
      {userOtherBookings.length > 0 ? (
        userOtherBookings.map((booking) => (
          <BookingLine
            key={booking.platform_reserv_code}
            {...booking}
            onClick={() => {
              setSelectedBooking(booking);
              setActivePage("details");
            }}
          />
        ))
      ) : (
        <h4>No other bookings found!</h4>
      )}
    </div>
  );
}

export default BookingDetails;
